<template lang="en">
    <section>
     <loader :is-visible="isLoading"></loader>
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle  py-0">
                    <h4 name="header" class="modal-title mt-2 pt-1">
                        {{ $t('AssignDepartment') }}
                    </h4>
                    <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em> </button>
                </div>
                <div class="listing py-3 px-3">
                    <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';

export default {
    props: {
        selectedRecordValues: {
            type: Array,
            required: true
        },
        AssignDpt: {
            
            type: Array,
            required: true
        }
    },
    data() {
        return {
            FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                            astype: "CustomSelectField",
                            label: this.$t('AppWebsite'),
                            name: "AppWebsite",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                // showAddIcon: true,
                                //  onAddButtonClick: this.OpenAddTagNamePopup,
                            },
                            validationRules: {
                                "required": true
                            },
                            
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "category_id",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                            },
                            // validationRules: {
                            //     "required": true
                            // },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('MaxDuration'),
                            name: "max_duration",
                            value: "",
                            placeholder: "",
                            // validationRules: {
                            //     "required": true
                            // },
                        },
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [{
                            astype: "MultiSelectField",
                            label: this.$t('Productive'),
                            name: "Productive",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeDepartment,
                                onRemove: this.removeRecord,
                               // onRemove($event, field, meta)
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('UnProductive'),
                            name: "UnProductive",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeDepartment,
                                onRemove: this.removeRecord,

                            },
                            validationRules: {
                               "required": true
                            },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('Neutral'),
                            name: "Neutral",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeDepartment,
                                onRemove: this.removeRecord,

                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    ]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            DataLoaded: 0,
            TagsSelectedArray: [],
            departmentList: [],
            newDepartmentlist: [],
            ListData: [],
            TagsSelectedArray1: [],
            tagsData: [],
            TagsSelectedArray2: [],
            TagsSelectedArray3: [],
            MultiArray: [],
            obj: '',
            isLoading:false,
          //  isAddAssignDept:false
        }
    },
    created: function () {
        this.GetDepartmentName();   
        this.pushList();
        this.GetCategoryList();
        this.FetchData(this.AssignDpt);
        this.DataLoaded = 1;
    },
    methods: {
        FetchData: function (ths) {
            var vm = this;
            vm.isLoading=true;
            var appId=(ths==undefined || ths.ApplicationId==undefined)?0:ths.ApplicationId;
            var url = `applicationId=${appId}`;
            DataService.GetAssignDepartmentByApplicationId(url).then(response => {

                if(!(ths==undefined || ths.ApplicationId==undefined)){
                    var data = {
                            name: ths.category_name,
                            value: ths.category_id
                        }
                        vm.FormSchema[0].Data[1].value = data.value;
                        var obj1 = ths.max_duration
                        vm.FormSchema[0].Data[2].value = obj1;

                        var obj2 = {
                            label: ths.application_display_name,
                            value: ths.ApplicationId
                        }
                        vm.FormSchema[0].Data[0].value = obj2;
                }
                vm.tagsData = response.data
                vm.tagsData.forEach((item, index) => {
                vm.newDepartmentlist.forEach((itm, indx) => {
                    if (item.productive_type == "Productive") {
                        
                            if(item.department_id==itm.value)
                            {
                                vm.newDepartmentlist[indx].label= item.department_name;
                                vm.newDepartmentlist[indx].value= item.department_id;
                                vm.newDepartmentlist[indx].productive_type='Productive';
                            }
                        
                    } else if (item.productive_type == "UnProductive") {
                        
                            if(item.department_id==itm.value)
                            {
                            vm.newDepartmentlist[indx].label= item.department_name;
                            vm.newDepartmentlist[indx].value= item.department_id;
                            vm.newDepartmentlist[indx].productive_type='UnProductive';
                            }
                        
                    } else if (item.productive_type == "Neutral") {
                       
                            if(item.department_id==itm.value)
                            {
                            vm.newDepartmentlist[indx].label= item.department_name;
                            vm.newDepartmentlist[indx].value= item.department_id;
                            vm.newDepartmentlist[indx].productive_type='Neutral';
                            }
                    }
                 })
                })
                this.FormSchema[1].Data[0].config.options= this.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Productive');
                this.FormSchema[1].Data[1].config.options= this.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='UnProductive');
                this.FormSchema[1].Data[2].config.options= this.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Neutral');

                this.FormSchema[1].Data[0].value= this.newDepartmentlist.filter(x=>x.productive_type=='Productive');
                this.FormSchema[1].Data[1].value= this.newDepartmentlist.filter(x=>x.productive_type=='UnProductive');
                this.FormSchema[1].Data[2].value= this.newDepartmentlist.filter(x=>x.productive_type=='Neutral');
                this.validateForm();
                vm.isLoading=false;
            })
        },
         onChangeDepartment(e, x){
            if( x.name=="Productive"){
                let i = this.newDepartmentlist.map(item => item.name).indexOf(event.target.textContent);
                this.newDepartmentlist[i].productive_type='Productive'
            }
           if ( x.name=="UnProductive"){
                let i = this.newDepartmentlist.map(item => item.name).indexOf(event.target.textContent);
                this.newDepartmentlist[i].productive_type='UnProductive'
            }
          if ( x.name=="Neutral") {
                let i = this.newDepartmentlist.map(item => item.name).indexOf(event.target.textContent);
                this.newDepartmentlist[i].productive_type='Neutral'
             }

            this.FormSchema[1].Data[0].config.options= this.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Productive');
            this.FormSchema[1].Data[1].config.options= this.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='UnProductive');
            this.FormSchema[1].Data[2].config.options= this.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Neutral');

            this.validateForm();

        },
        removeRecord(event, field, meta)
        {
           var vm=this;
           let i = vm.newDepartmentlist.map(item => item.value).indexOf(event.value);
           if(meta.name=="Productive")
                vm.newDepartmentlist[i].productive_type=null;
            if(meta.name=="UnProductive")
                vm.newDepartmentlist[i].productive_type=null;
            if(meta.name=="Neutral")
                vm.newDepartmentlist[i].productive_type=null;
           vm.FormSchema[1].Data[0].config.options= vm.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Productive');
           vm.FormSchema[1].Data[1].config.options= vm.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='UnProductive');
           vm.FormSchema[1].Data[2].config.options= vm.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Neutral');
           this.validateForm();
        },
        validateForm()
        {
            var vm=this;
            if(vm.newDepartmentlist.filter(x=>x.productive_type=='Productive').length>0)
            {
              this.FormSchema[1].Data[1].validationRules.required=false;
              this.FormSchema[1].Data[2].validationRules.required=false;
            }
            if(vm.newDepartmentlist.filter(x=>x.productive_type=='UnProductive').length>0)
            {
              this.FormSchema[1].Data[0].validationRules.required=false;
              this.FormSchema[1].Data[2].validationRules.required=false;
            }
            if(vm.newDepartmentlist.filter(x=>x.productive_type=='Neutral').length>0)
            {
              this.FormSchema[1].Data[0].validationRules.required=false;
              this.FormSchema[1].Data[1].validationRules.required=false;
            }
            if(vm.newDepartmentlist.filter(x=>x.productive_type=='Productive').length==0 && vm.newDepartmentlist.filter(x=>x.productive_type=='UnProductive').length==0 && vm.newDepartmentlist.filter(x=>x.productive_type=='Neutral').length==0)
            {
                this.FormSchema[1].Data[0].validationRules.required=true;
                this.FormSchema[1].Data[1].validationRules.required=true;
                this.FormSchema[1].Data[2].validationRules.required=true;
            }
        },
        async onSubmit(formfields, e) {
            
            var vm = this;
            vm.isLoading = true;
            if (vm.AssignDpt != null) {
                var Appid = vm.AssignDpt.ApplicationId;
            } else {
                Appid = 0;
            }
            var formData = new FormData();
            formfields.Productive.forEach(element => {

                let obj = {
                    department_id: element.value,
                    productive_type: 'Productive'
                }
                vm.MultiArray.push(obj)
            });

            formfields.UnProductive.forEach(element => {

                let obj = {
                    department_id: element.value,
                    productive_type: 'UnProductive'
                }
                vm.MultiArray.push(obj)
            });

            formfields.Neutral.forEach(element => {

                let obj = {
                    department_id: element.value,
                    productive_type: 'Neutral'
                }
                vm.MultiArray.push(obj)
            });
        var obj={};
            if(formfields.category_id==undefined){
                obj = {
                DepartmentAssignId: 0,
                AppId: Appid==undefined?0:Appid,
                CategoryId: formfields.category_id,
                MaxDration: formfields.max_duration==undefined?0:formfields.max_duration,
                Websites: Array.prototype.map.call(formfields.AppWebsite, function (item) {
                    return item.value;
                }).join(","),
                Productivity: vm.MultiArray

            }
            }
            else{
                obj = {
                DepartmentAssignId: 0,
                AppId: Appid==undefined?0:Appid,
                CategoryId: formfields.category_id,
                MaxDration: formfields.max_duration==undefined?0:formfields.max_duration,
                Websites: Array.prototype.map.call(formfields.AppWebsite, function (item) {
                    return item.value;
                }).join(","),
                Productivity: vm.MultiArray
            }
            }

         

            formData.append("postString", JSON.stringify(obj));
          //  var objectPostString = JSON.stringify(obj);
            await DataService.SaveAssignDepartment(obj).then(response => {
                vm.isLoading = false;
                if (response.data.status == 1) {
                    vm.ShowAlert(vm.$t('Departmentsuccessfullyassigned'), "success", false, vm.$t("Alert"));
                    vm.$parent.FetchData();
                    vm.closePopup();
                } else if (response.data.status == 2) {
                    vm.ShowAlert(vm.$t('Departmentsuccessfullyupdated'), "success", true, vm.$t('Alert'));
                    vm.$parent.FetchData();
                    vm.closePopup();
                }
                else
                {
                    vm.ShowAlert(vm.$t('Failure'), "error", true, vm.$t('Alert')); 
                }
                
            })
        },
        async GetCategoryList() {
            var vm = this;
            vm.isLoading = true;
            var url=`pageSize=500&sortBy=category_name&sortExp=ASC`
            await DataService.GetCategoryListing(url)
                .then(function (response) {
                    JSON.parse(response.data).data.forEach(item => {
                        vm.obj = {
                            name: item.category_name,
                            value: item.category_id
                        }
                        vm.FormSchema[0].Data[1].config.options.push(vm.obj);
                           
                                             
                    });

                    vm.isLoading = false;
                });
        },
        async GetDepartmentName() {
            var vm = this;
            vm.isLoading = true;
            var url = ``;
            await DataService.GetDepartmentList().then(response => {
                    vm.departmentList = response.data;
                    vm.departmentList.forEach((item) => { 
                        vm.newDepartmentlist.push({
                            name: item.text,
                            value: item.value,
                            productive_type:null
                        });
                    })
                    vm.FormSchema[1].Data[0].config.options= vm.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Productive');
                    vm.FormSchema[1].Data[1].config.options= vm.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='UnProductive');
                    vm.FormSchema[1].Data[2].config.options= vm.newDepartmentlist.filter(x=>x.productive_type==null || x.productive_type=='Neutral');
                    vm.isLoading = false;
                })
                .catch((err) => {
                    console.error(err);
                });
        },
        closePopup() {
            var vm = this;
            vm.TagsSelectedArray = [];
            vm.selectedRecordValues = [];
            vm.$parent.CloseAssignDepartmentClick();
        },
        pushList() {
            
            var vm = this
            vm.isLoading=true;
            var showCategory=true;
            var cat_id="";
            vm.selectedRecordValues.forEach((item) => {
                vm.TagsSelectedArray.push({
                    label: item.application_display_name,
                    value: item.ApplicationId
                })
                vm.FormSchema[0].Data[0].config.options.push({
                    name: item.application_display_name,
                    value: item.ApplicationId
                })
                
            })
            
            vm.FormSchema[0].Data[0].value = vm.TagsSelectedArray;
            if(vm.AssignDpt==undefined)
            {

           
            if(vm.selectedRecordValues.length>0)
            {
                if(vm.selectedRecordValues[0].category_id=='')
                {
                    vm.FormSchema[0].Data[1].value= "";
                }
                else
                {
                    vm.FormSchema[0].Data[1].value= vm.selectedRecordValues[0].category_id;
                }
            }
            else
            {
                if(vm.selectedRecordValues[0].category_id=='')
                {
                vm.FormSchema[0].Data[1].value= "";
                }
                else
                {
                    vm.FormSchema[0].Data[1].value= vm.selectedRecordValues.category_id;
                }
            }
        }

            vm.isLoading=false;
        }
    }

}
</script>
