<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle  py-0">
                        <h4 name="header" class="modal-title mt-2 pt-1">
                            {{ $t('ProductivityForDepartment') }}
                        </h4>
                        <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em> </button>
                    </div>
                    <div class="listing py-3 px-3">
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th>
                                            <span>Productive</span>
                                        </th>
                                        <th class="nonresize">
                                            <span>Unproductive</span>
                                        </th>
                                        <th data-width="5%" class="nonresize">
                                            <span>Neutral</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(items) in tagArrObj" v-bind:key="items.department_name">
    
                                        <td>
                                            <span class="text-overflow-dynamic-container-text-center">                                           
                                                {{items.product_name_productive}}
                                            </span>
                                        </td>
    
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{items.product_name_unproductive}}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{items.product_name_neutral}}
                                            </span>
                                        </td>
                                    </tr>
                                    <tr>
                                        <!-- <td v-if="NoRecordLength==0" colspan="7" class="text-danger text-center no-record">No Record Found</td> -->
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
    
                </div>
            </div>
        </div>
    </div>
    </template>
    
    <script>
    import DataService from '../../services/DataService';
    export default {
        props: {
            AssignDpt: {
                type: Array,
                required: true
            }
        },
        data() {
            return {
                NoRecordLength: 0,
                TagsSelectedArray: [],
                tagArrObj:[]
            }
        },
        created: function () {
            this.FetchData(this.AssignDpt);
        },
        methods: {
            FetchData: function (ths) {
                var vm = this;
                var url = `applicationId=${ths}`;
                DataService.GetAssignDepartmentByApplicationId(url).then(response => {
                        vm.TagsSelectedArray = response.data
                        var biggest=[];
                       // debugger;
                        var newArrProductive=vm.TagsSelectedArray.filter(x=>x.productive_type=="Productive")
                        biggest.push(newArrProductive.length);
                        var newArrUnProductive=vm.TagsSelectedArray.filter(x=>x.productive_type=="UnProductive")
                        biggest.push(newArrUnProductive.length)
                        var newArrNuetral=vm.TagsSelectedArray.filter(x=>x.productive_type=="Neutral")
                        biggest.push(newArrNuetral.length)
                        var bigValue= biggest.sort(function (a, b) {
                            return a > b ? 1 : (a < b ? -1 : 0);
                            }).slice(-1);
    
                        for(i=0;i<bigValue;i++){
                            var objA={};
                            objA.product_name_productive='';
                            objA.product_name_unproductive='';
                            objA.product_name_neutral='';
                            vm.tagArrObj.push(objA);
                        }
    
                        for(var i=0;i<newArrProductive.length;i++){
                            vm.tagArrObj[i].product_name_productive=newArrProductive[i].department_name;
    
                        }
    
                        for(var i=0;i<newArrUnProductive.length;i++){
                            vm.tagArrObj[i].product_name_unproductive=newArrUnProductive[i].department_name;
    
                        }
                        for(var i=0;i<newArrNuetral.length;i++){
                            var objA={};
                            vm.tagArrObj[i].product_name_neutral=newArrNuetral[i].department_name;
                        }
                        })
                    },
                    closePopup() {
                        var vm = this;
                        vm.$parent.CloseProductivity();
                    }
                }
    
            }
    </script>
    